.blog
  height: 100vh
  background: $gradient-bottom
  &__container
    height: 100vh
    position: relative
    padding-top: 185px
    padding-bottom: 20px
    display: flex
    flex-direction: column
    justify-content: space-between
    // align-items: flex-start
    &::before
      content: ''
      position: absolute
      top: 226px
      right: 0
      width: 206px
      height: 320px
      background-color: rgba($bgc, 0.1)
    &::after
      content: ''
      position: absolute
      top: 20px
      right: 0
      width: 526px
      height: 206px
      background-color: rgba($bgc, 0.1)


  &__head

  &__slider
    margin-left: 0
    height: 420px
    width: 100%

  &__slider-wrap
    height: 420px

  &__slide
    display: flex
    align-items: center
    // flex: 1 1 100%

  &__image
    margin-right: 75px
    width: 690px
    height: 420px
    // position: relative

    // padding-bottom: 60%

  &__info
    display: flex
    flex-direction: column
    align-items: center

  &__slide-head
    @include font(700, 52, 60)
    margin-bottom: 15px

  &__slide-btn
    font-size: 20px

  &__navigation
    // content: ''
    position: absolute
    top: 50%
    left: 75%
    z-index: 1
    // width: 250px
    // height: 114px
    // background-color: rgba($black, 0.5)
    transform: translate(0, -50% )
    &::before
      content: ''
      position: absolute
      top: 50%
      left: -360px
      width: 360px
      height: 2px
      transform: translateY(-50%)
      background-color: #fff
      z-index: -1

.navigation
  &__btn
    width: 250px
    height: 114px
    cursor: pointer
    &-prev
      // transform: rotate(90deg)
    &-fill
      width: 100%
      height: 100%
      // transition: all 0.7s cubic-bezier(0.76, -0.76, 0.25, 1.26)
      svg
        // width: 250px
        // height: 114px
    &-next
      position: relative

      // transform: rotate(-90deg)
