h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a,
a:visited,
a:hover {
  color: inherit;
  text-decoration: none;
  outline: none;
}

@font-face {
  font-family: "Helvetica";
  src: url("../fonts/Helvetica-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Helvetica";
  src: url("../fonts/Helvetica-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Spectral";
  src: url("../fonts/Spectral-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Spectral";
  src: url("../fonts/Spectral-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
:root {
  --widthScrollBar: 17px;
  --quantityHeroSlider: 4;
}

body {
  font-family: "Helvetica", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.8125;
}

.heading {
  font-family: "Helvetica", sans-serif;
  font-weight: 700;
  font-size: 100px;
  line-height: 0.95;
  letter-spacing: -0.05em;
}

html {
  scroll-behavior: smooth;
}

body {
  background: linear-gradient(180deg, #002535 0%, transparent 100%) #003851;
  color: #FFFFFF;
}
body._lock {
  overflow: hidden !important;
  height: 100% !important;
}

html,
body {
  height: 100%;
  margin: 0;
}

ul, li {
  padding: 0;
}

h1, h2, h3 {
  margin: 0;
}

input {
  outline: none;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  overflow: hidden;
  position: relative;
}

.main {
  /*  flex-grow - увеличиваться в размере: 0 зап-т, 1 раз-т
  * flex-shrink - уменьшаться в размере: 0 зап-т, 1 раз-т
  * flex-basis - определяет размер элемента по умолчанию
  * */
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

[class*=__container] {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 20px;
}

._ibg {
  position: relative;
}
._ibg img {
  object-fit: cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.logo--header {
  height: 50px;
}

.btn-reset {
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  outline: none;
}

.btn {
  display: inline-block;
  background: #A7FF4F;
  border-radius: 30px;
  padding: 15px 25px;
  color: #000000 !important;
  font-weight: 700;
  font-size: 20px;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
  transition: all 0.3s linear;
}

.unvisible {
  position: absolute;
  top: 0;
  left: -3000px;
  overflow: hidden;
  width: 0;
  height: 0;
}

.header {
  padding: 27px 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 5;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 120px;
  position: relative;
}
.header__logo {
  width: 94px;
  font-family: "Spectral", serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 0.9166666667;
  color: #FFFFFF;
}
.header__menu {
  display: block;
}

.menu__body {
  position: absolute;
  top: -27px;
  right: -100vw;
  width: 500px;
  height: 100vh;
  padding: 150px 44px 50px 44px;
  background: linear-gradient(180deg, #002535 0%, transparent 100%) #003851;
  opacity: 0.9;
  overflow: auto;
  transition: right 1s ease;
  border-left: 3px solid #FFFFFF;
}
.menu__body._active {
  right: 102px;
}
.menu__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.menu__item {
  position: relative;
  font-family: "Spectral", serif;
  font-weight: 600;
  font-size: 26px;
  line-height: 0.7692307692;
  color: #FFFFFF;
  text-transform: uppercase;
  transition: color 0.3s ease;
}
.menu__item:not(:last-child) {
  margin-bottom: 39px;
}
.menu__item::before {
  position: absolute;
  content: "";
  display: block;
  bottom: -6px;
  width: 0%;
  height: 2px;
  background-color: #FFFFFF;
  transition: width 0.5s ease;
}
@media (max-width: 768px) {
  .menu__item {
    margin-bottom: 40px;
    font-weight: 800;
    font-size: 18px;
  }
  .menu__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 10px;
  }
}
.menu__icon {
  display: block;
  position: relative;
  flex: 0 0 40px;
  width: 40px;
  height: 11px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  outline: none;
  z-index: 5;
}
.menu__icon span {
  position: absolute;
  top: calc(50% - 3px);
  left: 0;
  width: 30px;
  height: 3px;
  background-color: #FFFFFF;
  transition: all 0.7s cubic-bezier(0.76, -0.76, 0.25, 1.26);
}
.menu__icon span:first-child {
  top: 0;
}
.menu__icon span:last-child {
  top: auto;
  bottom: 0;
  right: 0;
  left: auto;
}
.menu__icon._active {
  animation: menuSpan 1s ease 0.3s forwards;
}
.menu__icon._active span {
  transition: all 0.4s cubic-bezier(0.76, -2.76, 2.25, 1.26);
}
.menu__icon._active span:first-child {
  top: calc(50% - 3px);
  transform: rotate(-45deg);
}
.menu__icon._active span:last-child {
  top: calc(50% - 3px);
  transform: rotate(45deg);
  left: 0;
}

.hero {
  position: relative;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.hero__container {
  position: relative;
  z-index: 2;
  height: 100%;
}
.hero__slider {
  height: 100%;
  margin-right: 80px;
  padding: 50px 0;
  position: relative;
}
.hero__slider::before {
  content: "";
  position: absolute;
  height: 100%;
  right: 1px;
  top: 0;
  bottom: 0;
  width: 1px;
  background: rgba(255, 255, 255, 0.5);
}
.hero__slide {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}
.hero__slide-head {
  max-width: 590px;
}
.hero__slide-btn {
  margin-right: 20px;
  font-weight: 700;
  font-size: 30px;
  line-height: 1;
}
.hero__pagination {
  right: 3px !important;
  top: 0 !important;
  width: 5px !important;
  left: auto !important;
}
.hero__pagination span {
  width: 5px;
  background: transparent !important;
  height: calc(100% / var(--quantityHeroSlider));
  border-radius: 5px !important;
  transition: all 1s ease;
}
.hero__pagination span.swiper-pagination-bullet-active {
  background: #FFFFFF !important;
}

.whoweare {
  height: 100vh;
}
.whoweare__container {
  height: 100vh;
  position: relative;
  padding-top: 185px;
  padding-bottom: 140px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.whoweare__container::before {
  content: "";
  position: absolute;
  top: 20px;
  right: 20px;
  width: 526px;
  height: 206px;
  background-color: rgba(34, 100, 110, 0.1);
}
.whoweare__container::after {
  content: "";
  position: absolute;
  bottom: 20px;
  left: 20px;
  width: 690px;
  height: 206px;
  background-color: rgba(34, 100, 110, 0.1);
}
.whoweare__head {
  margin-bottom: 50px;
}
.whoweare__info {
  display: flex;
  column-gap: 20px;
  flex-wrap: wrap;
  justify-content: flex-end;
}
.whoweare__text {
  flex: 0 1 23.9%;
  min-width: 250px;
  border-top: 2px solid #FFFFFF;
  padding-top: 10px;
}
.whoweare__btn {
  margin-top: 30px;
}

.misiunea {
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: background-image 0.3s ease-out 0s;
}
.misiunea__container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.misiunea__head {
  padding-left: 25.35%;
  margin-bottom: 170px;
}
.misiunea__list {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  column-gap: 20px;
}
.misiunea__item {
  flex: 0 1 23.9%;
  min-width: 250px;
  border-top: 2px solid #FFFFFF;
  padding-top: 15px;
  padding-left: 17px;
  cursor: pointer;
}
.misiunea__item p {
  font-weight: 400;
  font-size: 52px;
  line-height: 0.9615384615;
}
.misiunea__item p:not(:last-child) {
  margin-bottom: 20px;
}
.blog {
  height: 100vh;
  background: linear-gradient(180deg, #002535 0%, transparent 100%) #22646E;
}
.blog__container {
  height: 100vh;
  position: relative;
  padding-top: 185px;
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.blog__container::before {
  content: "";
  position: absolute;
  top: 226px;
  right: 0;
  width: 206px;
  height: 320px;
  background-color: rgba(34, 100, 110, 0.1);
}
.blog__container::after {
  content: "";
  position: absolute;
  top: 20px;
  right: 0;
  width: 526px;
  height: 206px;
  background-color: rgba(34, 100, 110, 0.1);
}
.blog__slider {
  margin-left: 0;
  height: 420px;
  width: 100%;
}
.blog__slider-wrap {
  height: 420px;
}
.blog__slide {
  display: flex;
  align-items: center;
}
.blog__image {
  margin-right: 75px;
  width: 690px;
  height: 420px;
}
.blog__info {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.blog__slide-head {
  font-weight: 700;
  font-size: 52px;
  line-height: 1.1538461538;
  margin-bottom: 15px;
}
.blog__slide-btn {
  font-size: 20px;
}
.blog__navigation {
  position: absolute;
  top: 50%;
  left: 75%;
  z-index: 1;
  transform: translate(0, -50%);
}
.blog__navigation::before {
  content: "";
  position: absolute;
  top: 50%;
  left: -360px;
  width: 360px;
  height: 2px;
  transform: translateY(-50%);
  background-color: #fff;
  z-index: -1;
}

.navigation__btn {
  width: 250px;
  height: 114px;
  cursor: pointer;
}
.navigation__btn-fill {
  width: 100%;
  height: 100%;
}
.navigation__btn-next {
  position: relative;
}

@media (min-width: 992px) {
  .menu__item:hover::before {
    width: 100%;
  }
  .menu__item:hover:last-child a {
    color: #FFFFFF;
  }
}
@keyframes move-down {
  0% {
    transform: translate(0, 0) scale(1);
  }
  30% {
    transform: translate(0px, 100px) scale(1.1);
  }
  45% {
    transform: translate(0px, 70px) scale(1);
  }
  70% {
    transform: translate(0px, 100px) scale(1.1);
  }
  100% {
    transform: translate(0px, 90px) scale(1);
  }
}
@keyframes menuSpan {
  0% {
    left: 0;
  }
  100% {
    left: -400px;
  }
}